import React, { useEffect, useState } from "react";
import axios from "axios";
import GridCards from "./GridCards";
import Loading from "./Spninner";
import { Row } from "antd";

import StyleContainer from "./styled/container";
import Button from "./styled/btn";
import styled from "styled-components";

//,        
import { useRecoilState, useRecoilValue } from "recoil";
import { genresState, resultMovieState } from "../state/atoms";
import { useHistory } from "react-router-dom";

import dot3 from "./icon/dot-3.png";
import Progress from "./styled/dot";

const FilterMovie = ({ onPrev, onNext }) => {
    const history = useHistory();
    const [genres, setGenres] = useRecoilState(genresState);
    const [movieData, setMovieData] = useRecoilState(resultMovieState);
    const [loading, setLoading] = useState(true);
    const [movies, setMovies] = useState([]);
    const [selectedMovie, setSelectedMovie] = useState(0);
    const [selectedMovieTitle, setSelectedMovieTitle] = useState(0);

    function shuffle(array) {
        array.sort(() => Math.random() - 0.5);
    } // api     


    let testapi = `https://yts.mx/api/v2/list_movies.json?minimum_rating=8.5&sort_by=year`;
    let api = `/back/filter/movies`;


    useEffect(() => {
        async function loadData() {
            setGenres({ ...genres });
            try {
                const response = await axios.post(api, genres);
                console.log(response);
                setMovies(response.data);
                console.log("API  data", ...movies);
                setLoading(false);
            } catch (e) {
                console.log("axios get Error");
            }
        }
        loadData();
    }, []);
    console.log("movies: ", movies);

    useEffect(() => {
        shuffle(movies);
    }, [movies]);

    const firstMovies = movies.slice(0, 6);
    console.log("6  data", firstMovies);

    useEffect(() => {
        console.log(" id", selectedMovie);
    }, [selectedMovie]);

    const onClickHandler = async () => {
        //    
        const res = await axios
            .get(`/back/filter/recommend/${selectedMovie}`)
            .then((res) => setMovieData(res.data))
            .catch((e) => console.log(e))
            .then(() => onNext());
    };

    return (
        <div>
            {loading ? (
                <Loading color="#CC455C" title="  " />
            ) : (
                <>
                    <div style={{ textAlign: "center" }}>
                        <Progress src={dot3} alt="progress" />
                    </div>
                    <StyleContainer>
                        <h1>    .</h1>
                        <GridBox>
                            <Row gutter={[60, 30]}>
                                {/*gutter Col     */}
                                {movies &&
                                    firstMovies.map((movie, index) => (
                                        <React.Fragment key={index}>
                                            <GridCards
                                                image={movie.poster_url}
                                                movieName={movie.movie_title}
                                                url={movie.url}
                                                id={movie.movie_id}
                                                setSelectedMovie={
                                                    setSelectedMovie
                                                }
                                                setSelectedMovieTitle={
                                                    setSelectedMovieTitle
                                                }
                                                track={movie.preview_url}
                                                xs={Number(12)}
                                                circle="true"
                                            />
                                        </React.Fragment>
                                    ))}
                            </Row>
                        </GridBox>
                        {selectedMovieTitle === 0 ? (
                            <div></div>
                        ) : (
                            <h2 style={{ marginBottom: "19px" }}>
                                {selectedMovieTitle} OST  .
                            </h2>
                        )}
                        <div style={{ marginBottom: "5px" }}>
                            <Button onClick={onPrev}></Button>
                            <Button
                                disabled={!selectedMovie}
                                onClick={onClickHandler}
                            >
                                
                            </Button>
                        </div>
                    </StyleContainer>
                </>
            )}
        </div>
    );
};

const GridBox = styled.div`
    width: 50%;
    display: flex;
    margin-bottom: 30px;
    margin-top: 30px;
`;

export default FilterMovie;
